import {Component, OnInit, AfterViewInit} from '@angular/core';

/*import { ForcastService } from '../forcast.service.2';*/

declare let $: any;

@Component({
  selector: 'app-forcast-pannel',
  templateUrl: './forcast-pannel.component.html',
  styleUrls: ['./forcast-pannel.component.css'],
})
export class ForcastPannelComponent implements OnInit {
  public key : string = '68af96f1519d4d1388c2893774ec8e3b';
  public locate : string;
  public url;

  public update;

  public city:string;
  public code_d:number;
  public date;
  public l_tmp;
  public h_tmp;
  public cond;
  public src_tod;

  public tw_code_d:number;
  public tw_cond;
  public tw_l_tmp;
  public tw_h_tmp;
  public src_tw;

  public th_code_d:number;
  public th_cond;
  public th_l_tmp;
  public th_h_tmp;
  public src_th;

  constructor() {}

  /*获取当前城市*/
  getCurrentCity(){
    $.ajax({
      type: "get",
      url: 'http://api.map.baidu.com/location/ip?ak=lKOjaLpi6DbtRHgQo9SMh45hoRyfymzd&coor=bd09ll',
      async: true,
      dataType: "jsonp",
      success: (data) => {
        this.locate =  (data.content.address_detail.city).slice(0,-1);

        this.url = 'https://free-api.heweather.com/v5/weather?city='+ this.locate +'&key='+this.key;
        this.getW();
      }
    });
  }

  /*显示天气数据*/
  showW(data){
    /*获取数据*/
    let weth = data.HeWeather5[0];
    //console.log(weth);


    /*今天天气数据*/
    this.city = weth.basic.city;
    this.code_d = weth.daily_forecast[0].cond.code_d;
    this.date = weth.daily_forecast[0].date;
    this.cond = weth.daily_forecast[0].cond.txt_d;
    this.l_tmp = weth.daily_forecast[0].tmp.min;
    this.h_tmp = weth.daily_forecast[0].tmp.max;
    this.src_tod = `./assets/imgs/weth/${this.code_d}.png`;

    this.update=weth.basic.update.loc;

    /*明天天气数据*/
    this.tw_code_d = weth.daily_forecast[1].cond.code_d;
    this.tw_cond = weth.daily_forecast[1].cond.txt_d;
    this.tw_l_tmp = weth.daily_forecast[1].tmp.min;
    this.tw_h_tmp = weth.daily_forecast[1].tmp.max;
    this.src_tw = `./assets/imgs/weth/${this.tw_code_d}.png`;

    /*后天天气数据*/
    this.th_code_d = weth.daily_forecast[2].cond.code_d;
    this.th_cond = weth.daily_forecast[2].cond.txt_d;
    this.th_l_tmp = weth.daily_forecast[2].tmp.min;
    this.th_h_tmp = weth.daily_forecast[2].tmp.max;
    this.src_th = `./assets/imgs/weth/${this.th_code_d}.png`;
  }

  getW() {
    $.ajax({
      type: "get",
      async: true,
      cache: false,
      url: this.url,
      success: (data) => this.showW(data),
      error: () => {
        alert("获取天气信息失败！");
      }
    });
  }

  ngOnInit() {
    this.getCurrentCity();
  }
  ngAfterViewInit() {

  }

}



