import { NgModule }           from '@angular/core';
import { CommonModule }   from '@angular/common';
import { HttpModule, JsonpModule } from '@angular/http';

import { ForcastComponent }   from './forcast.component';
import { ForcastRoutingModule }   from './forcast-routing.module';

import { ForcastLocComponent } from './forcast-loc/forcast-loc.component';

import { SearchPannelComponent } from './search-pannel/search-pannel.component';
import { ForcastPannelComponent } from './forcast-pannel/forcast-pannel.component';
import { MoreComponent } from './more/more.component';
import { StateComponent } from './state/state.component';
import { SetComponent } from './set/set.component';

import { requestOptionsProvider } from './default-request-options.service';
import { ForcastService } from './forcast.service.2';

import { CitySechService } from './city-sech.service';

@NgModule({
  imports:      [
    CommonModule,
    ForcastRoutingModule,
    HttpModule,
    JsonpModule
  ],
  declarations: [
    ForcastComponent,
    ForcastLocComponent,
    ForcastPannelComponent,
    SearchPannelComponent,
    MoreComponent,
    StateComponent,
    SetComponent
  ],
  providers:[
    CitySechService,
    requestOptionsProvider,
    ForcastService
  ],
})
export class ForcastModule { }



