import { Injectable } from '@angular/core';
import { Jsonp, Http, Response,URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
@Injectable()
export class ForcastService {
  times;
  url = 'https://free-api.heweather.com/v5/weather?city=shenzhen&key=68af96f1519d4d1388c2893774ec8e3b';

  constructor(private jsonp: Jsonp) {}
  getNative() {
    var params = new URLSearchParams();
    params.set("callback", "__ng_jsonp__.__req0.finished");

    this.jsonp.get(this.url, params).subscribe(res=> {
      console.log(res);
    }, err=> {
      console.log(err);
    });
  }
}









