import { NgModule }            from '@angular/core';
import { RouterModule, Routes }        from '@angular/router';

import { ForcastComponent }    from './forcast.component';
import { ForcastPannelComponent } from './forcast-pannel/forcast-pannel.component';
import { SearchPannelComponent } from './search-pannel/search-pannel.component';
import { MoreComponent } from './more/more.component';
import { StateComponent } from './state/state.component';
import { SetComponent } from './set/set.component';

const routes: Routes = [
  {
    path: '',
    component: ForcastComponent,
    children: [
          { path:'',redirectTo:'pannel',pathMatch:'full'},
          { path: 'pannel', component:ForcastPannelComponent},
          { path: 'search/:cityName', component:SearchPannelComponent},
          { path: 'more', component:MoreComponent},
          { path: 'state', component:StateComponent},
          { path: 'set', component:SetComponent}
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class ForcastRoutingModule {}



