import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { CitySechService } from '../city-sech.service';

declare let $: any;

@Component({
  selector: 'app-forcast-loc',
  templateUrl: './forcast-loc.component.html',
  styleUrls: ['./forcast-loc.component.css']
})
export class ForcastLocComponent implements OnInit {
  public city; //本地城市
  public userCity; //用户输入的城市
  public citys; //要传递的城市

  constructor(private citySechService: CitySechService ,
              private route: ActivatedRoute,
              private router: Router
  ) { }
  /*获取当前城市*/
  getCurrentCity(){
    $.ajax({
      type: "get",
      url: 'http://api.map.baidu.com/location/ip?ak=lKOjaLpi6DbtRHgQo9SMh45hoRyfymzd&coor=bd09ll',
      async: false,
      dataType: "jsonp",
      success: (data) => {
        this.city = this.userCity || (data.content.address_detail.city);
      }
    });
  }

  setCitys(val){
    this.userCity = val;
    if(!this.userCity){
      alert('请输入城市名！')
      return false;
    }

    this.citySechService.setCitys(this.userCity);
  }
  //************************************************************* */

  startSearch(){
    //console.log(this.userCity);
     this.router.navigate(['forcast/search/', this.userCity]);
  }
//************************************************************* */
  ngOnInit() {
    this.getCurrentCity();
  }
  ngAfterViewInit(){

  }

}



